'use strict';

// With background scripts you can communicate with popup
// and contentScript files.
// For more information on background script,
// See https://developer.chrome.com/extensions/background_pages

chrome.runtime.onMessage.addListener(async (request, sender, sendResponse) => {
  if (request.type === 'GREETINGS') {
    console.log("logging from background");
    sendResponse({
      message: 'my custom message12344',
    });
  }

  if (request.type === 'ANOTHER_GREERTINGS') {
    sendResponse({
      message: 'my custom message44',
    });
  }

  if (request.type === 'NEW_TAB'){
    // chrome.tabs.create({url: request.url});
    const madeTab = await chrome.tabs.create({
      url: "https://github.com/new/import"
    });

    // send message to content script
    // chrome.tabs.sendMessage(madeTab.id, {
    //   type: 'GREETINGS',
    //   payload: {
    //     message: 'Hello, my name is Con. I am from ContentScript.',
    //   },
    // });

    sendResponse({
      message: 'new tab created',
    });
  }
});
